	module.exports = {"data":{"markdownRemark":{"html":"<p>I've read a lot of science fiction and fantasy novels over the years, too many to go through in one post. So I'm going to start with the most recent one I really enjoyed, and go back from there.</p>\n<p>The Broken Earth trilogy starts with <a href=\"https://www.amazon.com/dp/B00H25FCSQ/ref=dp-kindle-redirect?_encoding=UTF8&#x26;btkr=1\">The Fifth Season</a>, by N. K. Jemisin. It won a Hugo Award (the highest honor in science fiction literature, for those of you who don't know) in 2016, and for me, at least, that award was well deserved.</p>\n<p>The raw emotions in the series are incredibly intense, and I have to say, these books are not for the faint of heart. A lot of terrible things happen to the main characters, so if you're looking for a cheery set of books, look elsewhere.</p>\n<p>However, if you're looking for a <em>good</em> set of books, pick these up. The worldbuilding alone is excellent, and so is everything else.</p>\n<p>The trilogy takes place on an earth-like planet called The Stillness, in which for thousands of years, major earthquakes have distrupted the progress of civilization, causing what is known as a Fifth Season every few decades. These seasons are devistating, wiping out whole communities and making survival difficult at best - not unlike the classic example of a nuclear winter, except caused by earthquake induced ash clouds and acid rain instead.</p>\n<p>One of the only reasons humanity has survived so long in this kind of situation is the existance of people called orogenes. Orogenes have the ability to draw in  ambient energy to control the earth. In doing so, they create a torus, and area around them which usually becomes very cold, very fast due to the sudden lack of heat. They're universilly hated by most people, mostly because they can just as easily cause an earthquake as stop one.</p>\n<p>In order to keep the peace, orogenes are rounded up and taken to The Fulcrum, where they act as what could be considered well-treated slaves who are sent out to try and prevent fifth seasons, and to stop any other earth-related problems around the Stillness.</p>\n<p>There's much more to the plot than that, and there's a lot I can't cover, including about the main character, due to spoilers.</p>\n<p>The writing in these books is sublime, but also surprisingly unique. The books take place in the present tense, which is rare enough as it is, but they also emply the first, second, and third person voices at various points. This is something I've almost never seen before, and yet Jemisin does it masterfully. It's a breath of fresh air from the regular style of writing you often see in fantasy.</p>\n<p>Anyway, that's my book review for today. I hope you enjoyed it, and see you next time!</p>","frontmatter":{"date":"June 24, 2018","path":"/broken-earth","title":"The Broken Earth Trilogy Review","tags":["Book Review","Fantasy","Worldbuilding"]}}},"pathContext":{}}

